
import axios from 'axios'
import { reactive, toRefs } from '@vue/reactivity'
import { message } from 'ant-design-vue'
import { computed, defineComponent } from '@vue/runtime-core'

import { FileInfo } from '@/type'

export default defineComponent({
  name: 'download-card',
  props: {
    fileInfo: {
      type: Object,
      default: (): FileInfo => {
        return { fileName: '', size: 0 }
      },
    },
    file: String,
  },
  setup(props) {
    const state = reactive({
      // 下载进度
      progress: 0,
      // 是否正在下载
      downing: false,
    })
    const compute = {
      sizeShow: computed(() => {
        let size = props.fileInfo?.size
        if (size < 1024) {
          return size + 'B'
        } else if (size < 1024 * 1024) {
          return (size / 1024).toFixed(2) + 'K'
        } else {
          return (size / 1024 / 1024).toFixed(2) + 'M'
        }
      }),
    }

    let cancelFunc: any = null

    // methods
    // 点击下载的回调
    const download = () => {
      if (state.downing && cancelFunc) {
        // 取消下载
        state.downing = false
        message.info('取消下载')
        cancelFunc('cancel download')
      } else if (!state.downing) {
        // 下载
        state.downing = true
        message.info('开始下载, 再次点击可取消下载')
        let isLocal = /^blob/.test(props.file as string)
        if (isLocal) {
          createTag(props.file as string)
        } else {
          let src = (props.file as string).replace(process.env.VUE_APP_DOWNLOAD, '/download')
          axios
            .get(src, {
              responseType: 'blob',
              onDownloadProgress: (progressEvent) => {
                state.progress = (progressEvent.loaded / progressEvent.total) * 100
              },
              cancelToken: new axios.CancelToken((c) => {
                cancelFunc = c
              }),
            })
            .then((res) => {
              let blob = res.data
              let src = URL.createObjectURL(blob)
              createTag(src)
            })
            .catch((err) => {
              if (err.message == 'cancel download') return
              // console.log(err);
              state.downing = false
              message.error('下载失败!')
            })
        }
      }

      // 创建a标签并下载
      function createTag(src: string) {
        let a = document.createElement('a')
        a.href = src
        a.download = props.fileInfo?.fileName

        a.click()
        message.success('下载成功')
        state.downing = false

        URL.revokeObjectURL(src)
        // a = null;
      }
    }

    return {
      ...toRefs(state),
      ...compute,
      download,
    }
  },
})
